.cardList {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin: -15px;
}

.movieCard {
    display: inline-flex;
    min-width: 200px;
    align-items: center;
    justify-content: center;
    margin: 15px;
    width: calc(100 / 2 * 30px) / 2;
}

.movieCard a {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 200px;
    text-decoration: none;
}

.movieCard img {
    max-width: inherit;
    margin-bottom: 20px;
}

.movieTitle {
    text-align: center;
    color: rgb(29, 28, 28);
}